<template>
  <div class="user">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-lx-cascades"></i> 点赞数据</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-input v-model="select_word" placeholder="筛选关键词" class="handle-input mr10"></el-input>
        <el-button type="primary" icon="search" @click="search">搜索</el-button>
      </div>
      <el-table :data="tableData" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column prop="arcid" label="文章ID" sortable >
        </el-table-column>
        <el-table-column prop="arcname" label="文章名" >
        </el-table-column>
        <el-table-column prop="likes" label="点赞量" >
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="1000">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'usertable',
  data() {
    return {
      url: './vuetable.json',
      tableData: [],
      cur_page: 1,
      multipleSelection: [],
      select_cate: '',
      select_word: '',
      del_list: [],
      is_search: false,
      editVisible: false,
      delVisible: false,
      totalpage:0,
      form: {
        arcid: '',
        arcname: '',
        likes: ''
      },
      idx: -1
    }
  },
  created() {
    axios('admin/getTotalLike').then(res =>{
      this.totalpage = res.data.page;
    });
    this.getData();
  },
  computed: {

  },
  methods: {
    handleCurrentChange(val) {
      this.cur_page = val;
      this.getData();
    },
    getData() {
      axios.post('admin/getLikeList', {
        page: this.cur_page
      }).then((res) => {
        this.tableData = Array(res.data.dataList)[0];
      })
    },
    search() {
      this.is_search = true;
    },
    formatter(row, ) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    handleEdit(index,) {
      this.idx = index;
      const item = this.tableData[index];
      this.form = {
        name: item.name,
        date: item.date,
        address: item.address
      }
      this.editVisible = true;
    },
    handleDelete(index, ) {
      this.idx = index;
      this.delVisible = true;
    },
    delAll() {
      const length = this.multipleSelection.length;
      let str = '';
      this.del_list = this.del_list.concat(this.multipleSelection);
      for (let i = 0; i < length; i++) {
        str += this.multipleSelection[i].name + ' ';
      }
      this.$message.error('删除了' + str);
      this.multipleSelection = [];
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 保存编辑
    saveEdit() {
      this.$set(this.tableData, this.idx, this.form);
      this.editVisible = false;
      this.$message.success(`修改第 ${this.idx+1} 行成功`);
    },
    // 确定删除
    deleteRow(){
      this.tableData.splice(this.idx, 1);
      this.$message.success('删除成功');
      this.delVisible = false;
    }
  }
}

</script>

